/* MyCv.css */
.cv-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    width: 100%; /* Change from 80% to 100% */
    max-width: 1400px;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  .back {
    margin-bottom: 20px;
  }
  
  .back h4 a {
    color: white;
    font-weight: bold;
    text-decoration: none;
  }
  
  .cv-iframe {
    height: 80vh;
    width: 1200px; 
    border: 2px solid white;
    border-radius: 8px;
    margin: auto;
  }

  .custom-cursor {
    position: fixed;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(109, 109, 226, 0.15), rgba(255, 255, 255, 0));
    pointer-events: none;
    transform: translate(-50%, -50%);
    animation: ripple 4.5s infinite;
    mix-blend-mode: difference;
    z-index: 1000;
  }
  
  @keyframes ripple {
    0% {
      transform: translate(-50%, -50%) scale(1);
      opacity: 1.5;
    }
    50% {
      transform: translate(-50%, -50%) scale(1.5);
      opacity: 1;
    }
    100% {
      transform: translate(-50%, -50%) scale(2);
      opacity: 0.8;
    }
  }
  
  /* Responsive Design */
  @media screen and (max-width: 768px) {
    .cv-iframe {
      width: 95%;
      height: 75vh;
    }
  }
  
  @media screen and (max-width: 480px) {
    .cv-iframe {
      width: 100%;
      height: 70vh;
    }
  }
  